@font-face {
    font-family: DM Sans;
    src: url(../../../assets/fonts/DMSans-VariableFont_opsz\,wght.ttf);
}

@font-face {
    font-family: Inter;
    src: url(../../../assets/fonts/Inter-VariableFont_opsz\,wght.ttf);
}

.addToCartContainer {
    background-color: #FFFFFF;
    height: fit-content;
    width: 80%;
    box-shadow: 1px 1px 10px rgba(201, 201, 201, 0.4);
    border-radius: 20px;
}

.productInfo {
    display: flex;
    flex-direction: column;
    gap: 0.75em;
    padding: 3em 3em 2em;
}

.productInfo div:nth-child(1) {
    font-family: DM Sans;
    font-weight: 700;
    font-size: 1.5rem;
    color: #000000;
}

.productInfo div:nth-child(2) {
    font-family: Inter;
    font-weight: 400;
    font-size: 1.25rem;
    color: #67696E;
    text-transform: uppercase;
}

.productInfo div:nth-child(3) {
    font-family: DM Sans;
    font-weight: 500;
    font-size: 1.5rem;
    color: #000000;
}

.line {
    border-bottom: 0.1em solid #E9EBEE;
    width: 100%;
}

.orderInfo {
    padding: 2em 3em 3em 3em;

    display: flex;
    flex-direction: column;
    gap: 3em;
}

.addToCartBtn {
    font-weight: 600;
    width: 100%;
}

@media only screen and (max-width: 600px) {
    .addToCartContainer {
        width: 15em;
    }

    .productInfo {
        padding: 1.5em 1.5em 1em;
    }

    .productInfo div:nth-child(1) {
        font-size: 1rem;
    }

    .productInfo div:nth-child(2) {
        font-size: 0.85rem;
    }

    .productInfo div:nth-child(3) {
        font-size: 1rem;
    }

    .orderInfo {
        padding: 1.25em;
        gap: 1.5em;
    }
}